



body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ae254c;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  background-color: purple;
  text-align: center;
}
.hello{
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #EBE5D1;
}
/* CoronaVirus Animation
Default speed is: 10s
MoveUpDown is a keyFrames that define the position*/
img{
  width: 5%;
  height: auto;
}

.corona{
  margin-left: 2vw;
  margin-right: 2vw;
  font-size: xx-large;
  font-family: 'Signika', sans-serif; padding-bottom: 10px; 
  width: 100%;
  text-align: center;
  font-stretch: ultra-expanded;
  letter-spacing: 1vw;
}

.movingvirus{
  height: 100%;
  width: 100%;
}
.animatediv1{
  animation: MoveUpDown 3s linear infinite;
  position: absolute;
  left: 5em;
  }
.animatediv2{
  animation: MoveUpDown 1.5s linear infinite;
  position: absolute;
  left: 10em;
  }
.animatediv3{
  animation: MoveUpDown 2s linear infinite;
  position: absolute;
  left: 15em;
  }
.animatediv4{
  animation: MoveUpDown 2.5s linear infinite;
  position: absolute;
  left: 1em;
  }
.animatediv5{
  animation: MoveUpDown 3.5s linear infinite;
  position: absolute;
  right: 1em;
  }
.animatediv6{
  animation: MoveUpDown 1.7s linear infinite;
  position: absolute;
  right: 5em;
  }
.animatediv7{
  animation: MoveUpDown 2.3s linear infinite;
  position: absolute;
  right: 10em;
  }
.animatediv8{
  animation: MoveUpDown 3.2s linear infinite;
  position: absolute;
  right: 15em;
  }
/* position of corona animation */
@keyframes MoveUpDown {
  0%, 100% {
    top: 590px;
  }
  50% {
    top: 630px;
  }
}
.search{
  visibility: hidden;
}

button{
  height: 25px;
  width: 100px;
  padding: 5px;
  border-radius: 8px;
}
button:hover{
  
  background-color: #d34154;
  animation: grow 3s ease-in-out;
  
  
}
@keyframes grow{
  0%{
    height: 25px;
    width: 100px;
  }
  100%{
    height: 55px;
    width: 200px;
  }
}
.blackButton{
  background-color: #194A6F;
  color: white;

  height: 55px;
  width: 200px;
}

.whiteButton{
  background-color: #b0ebce;
  color: black;
  height: 25px;
    width: 100px;

}
button:active{
  animation-play-state: paused;
}

.noout{
  visibility: hidden;
}
.yesout{
  visibility: show;
}


.uglytext{
  list-style-type: none;
  list-style-position: inside;
  
  position: absolute;
  width: 450px;
  text-align: center;
  align-self: center;
  margin: 0 auto;
  padding-left: 30%;
}
ul{
  background-color: whitesmoke;
  list-style-type: none;
  margin-top: 0px;
  margin-left: 0px;
  padding-left: 0px;
  padding-right: 0px;
}
input{
  width: 450px;
  height: 20px;
  
}
li{
  padding: 2px;
}

.parallax {
  /* The image used */
  background-image: url("code.jpg");

  /* Set a specific height */
  height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

textarea{
  background-color: #EBE5D1;
  border: none;
  resize: none;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  visibility: show;
}